.profile__img-edit {
    height: 120px;
    width: 120px;
    background-color: rgba(0, 0, 0, 0.8);
    position: absolute;
    top: 0px;
    left: 0px;
    opacity: 0;
    background-image: url(../../../images/penavatar.svg);
    background-position: center;
    background-repeat: no-repeat;
    transition: opacity linear 0.5s;
  }
  
  .profile__img-edit:hover {
    cursor: pointer;
    opacity: 1;
  }
  
  @media screen and (max-width: 767px) {
    .profile__img-edit {   
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        text-align: center;
    } 
  }